import {Component, OnInit} from '@angular/core';
import {Gravity} from '../Models';

@Component({
  selector: 'app-abvcalculator',
  templateUrl: './abvcalculator.component.html',
  styleUrls: ['./abvcalculator.component.css']
})
export class AbvcalculatorComponent implements OnInit {

  abv = null;

  gravity: Gravity = {
    OG: '1.0',
    FG: '1.0',
    ABV: null,
  };

  constructor() {
  }

  ngOnInit() {
  }

  ogfgToAlc(og, fg) {
    this.abv = (76.08 * (og - fg) / (1.775 - og)) * (fg / 0.794);
    this.gravity.ABV = this.abv.toFixed(3);
  }

  calculate(og, fg) {
    this.ogfgToAlc(og, fg);
  }
}



