import {Component, OnInit} from '@angular/core';
import {Yeastpitchingrate} from '../Models';

@Component({
  selector: 'app-yeastpitchingrate',
  templateUrl: './yeastpitchingrate.component.html',
  styleUrls: ['./yeastpitchingrate.component.css']
})
export class YeastpitchingrateComponent implements OnInit {

  today: any;
  viability: any;

  yeastpitchingrate: Yeastpitchingrate = {
    beertype: '',
    worthvolume: '',
    OG: '',
    neededyeastcells: '',
    yeastcells: '',
    yeastgrams: '',
    yeastdate: '',
    viability: 0.90,
    yeasttype: 'dry',
  };


  constructor() {
  }

  ngOnInit() {
  }

  calculate(beertype, volume, og, yeastdate) {
    yeastdate = (yeastdate.getTime() / 1000);
    this.today = (Date.now() / 1000).toFixed(0);
    this.viability = this.yeastpitchingrate.viability - (0.0006451612903 * ((this.today - yeastdate) / 86400));
    beertype = beertype * 1000000;
    volume = volume * 1000;
    og = (-1 * 616.868) + (1111.14 * og) - (630.272 * Math.pow(og, 2)) + (135.997 * Math.pow(og, 3));
    this.yeastpitchingrate.neededyeastcells = (beertype * volume * og).toFixed(0);
    this.yeastpitchingrate.yeastcells = this.viability * (11.5 * 20 * Math.pow(10, 9));
    this.yeastpitchingrate.yeastgrams = ((this.yeastpitchingrate.neededyeastcells / this.yeastpitchingrate.yeastcells) * 11.5).toFixed(2);
  }
}






