import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {RefractometerComponent} from './refractometer/refractometer.component';
import {AbvcalculatorComponent} from './abvcalculator/abvcalculator.component';
import {DashboardComponent} from './dashboard/dashboard.component';
import {HydrometercorrectionComponent} from './hydrometercorrection/hydrometercorrection.component';
import {Newtonslawofcooling, Primingcalc} from './Models';
import {NewtonslawofcoolingComponent} from './newtonslawofcooling/newtonslawofcooling.component';
import {YeastpitchingrateComponent} from './yeastpitchingrate/yeastpitchingrate.component';
import {PrimingcalcComponent} from './primingcalc/primingcalc.component';

const routes: Routes = [
  {
    path: '',
    redirectTo: '/dashboard',
    pathMatch: 'full'
  },
  {
    path: 'dashboard',
    component: DashboardComponent
  },
  {
    path: 'refractometer',
    component: RefractometerComponent
  },
  {
    path: 'abvcalc',
    component: AbvcalculatorComponent
  },
  {
    path: 'hydrometercorrection',
    component: HydrometercorrectionComponent
  },
  {
    path: 'newtons',
    component: NewtonslawofcoolingComponent
  },
  {
    path: 'yeast',
    component: YeastpitchingrateComponent
  },
  {
    path: 'primingcalc',
    component: PrimingcalcComponent
  }
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes)
  ],
  exports: [
    RouterModule,
  ]
})
export class AppRoutingModule {}



