import {Component, OnInit} from '@angular/core';
import {Temperaturecorrection} from '../Models';

@Component({
  selector: 'app-hydrometercorrection',
  templateUrl: './hydrometercorrection.component.html',
  styleUrls: ['./hydrometercorrection.component.css']
})
export class HydrometercorrectionComponent implements OnInit {

  corrected = null;


  temperaturecorrection: Temperaturecorrection = {
    SG: '',
    TR: '',
    TC: ''
  };

  constructor() {
  }

  ngOnInit() {
  }


  calculate(SG, TR, TC) {
    SG = parseFloat(SG);
    TR = (TR * (9 / 5) + 32);
    TC = (TC * (9 / 5) + 32);
    this.corrected = (SG * ((1.00130346 - (0.000134722124 * TR) + (0.00000204052596 * TR * TR) - (0.00000000232820948 * TR * TR * TR)) / (1.00130346 - (0.000134722124 * TC) + (0.00000204052596 * TC * TC) - (0.00000000232820948 * TC * TC * TC))));
    this.temperaturecorrection.correction = this.corrected.toFixed(3);
  }
}




