import {Component, OnInit} from '@angular/core';
import {Gravity} from '../Models';

@Component({
  selector: 'app-refractometer',
  templateUrl: './refractometer.component.html',
  styleUrls: ['./refractometer.component.css']
})
export class RefractometerComponent implements OnInit {

  brix = null;
  ri = null;
  fb = null;
  og = null;
  fg = null;
  abw = null;
  abv = null;

  gravity: Gravity = {
    OG: '1.0',
    FG: '1.0',
    CFG: null,
    ABV: null,
  };

  constructor() {

  }


  ngOnInit() {
  }


  sgToBrix(sg) {
    this.brix = (((182.4601 * sg - 775.6821) * sg + 1262.7794) * sg - 669.5622);
    return this.brix;
  }

  brixToAlc(og, fg) {
    this.fb = this.sgToBrix(fg);
    this.og = this.sgToBrix(og);
    this.fg = 1.001843 - (0.002318474 * this.og) - (0.000007775 * this.og * this.og) - (0.000000034 * this.og * this.og * this.og)
      + (0.00574 * this.fb) + (0.00003344 * this.fb * this.fb) + (0.000000086 * this.fb * this.fb * this.fb);
    this.ri = 1.33302 + (0.001427193 * this.fb) + (0.000005791157 * this.fb * this.fb);
    this.abw = 1017.5596 - (277.4 * this.fg) + this.ri * ((937.8135 * this.ri) - 1805.1228);
    this.abv = this.abw * (fg / 0.794);

    this.gravity.ABV = this.abv.toFixed(3);
  }

  calculate(og, fg) {
    this.brixToAlc(og, fg);
  }

}



