import {ChangeDetectorRef, Component, EventEmitter, OnInit} from '@angular/core';
import {MatTableDataSource} from '@angular/material';
import {Element, Item, Primingcalc} from '../Models';

@Component({
  selector: 'app-primingcalc',
  templateUrl: './primingcalc.component.html',
  styleUrls: ['./primingcalc.component.css']
})
export class PrimingcalcComponent implements OnInit {
  displayedColumns = ['name', 'co2_vol'];
  dataSource = new MatTableDataSource(ELEMENT_DATA);
  protected selectedItems: Set<Item> = new Set();

  co2inbeer = null;

  primingcalc: Primingcalc = {
    volume: '',
    co2: '',
    temperature: '',
    tablesugar: '',
  };

  constructor() {
  }

  ngOnInit() {

  }

  //calculate(volume, tablesugar, syringesize) {
  //  this.primingcalc.sugar = volume * tablesugar;
  //  this.primingcalc.water = syringesize * volume * 2;
  //}

  calculate(volume, co2, temperature) {
    temperature = (temperature * (9 / 5) + 32);
    this.primingcalc.tablesugar = ((co2 - ((3.0378 - (0.050062 * temperature) + (0.00026555 * temperature * temperature)) * volume / volume)) * 4 * volume).toFixed(3);
  }

  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }

  onRowClick(item: Item) {
    console.log(item);
  }

  trackById(index: number, item: Item) {
    return item.id;
  }

  isSelected(item: Item) {
    return this.selectedItems.has(item);
  }
}

const ELEMENT_DATA: Element[] = [
  {name: 'American ale', co2_vol: '2 - 2.5'},
  {name: 'American lager', co2_vol: '2.5 - 3'},
  {name: 'American stout', co2_vol: '2.5 - 3'},
  {name: 'American wheat', co2_vol: '2.5'},
  {name: 'Barleywine/old ale', co2_vol: '2 - 2.5'},
  {name: 'Belgian Ale', co2_vol: '2.5 - 4'},
  {name: 'Biere de garde', co2_vol: '2.5 - 3'},
  {name: 'Bock', co2_vol: '2.5'},
  {name: 'Brown ale', co2_vol: '1 - 1.5'},
  {name: 'California common', co2_vol: '2.5 - 3'},
  {name: 'English pale ale', co2_vol: '1 - 2'},
  {name: 'European lager', co2_vol: '2 - 2.5'},
  {name: 'Foreign stout', co2_vol: '2 - 2.5'},
  {name: 'German wheat', co2_vol: '2.5 - 4'},
  {name: 'IPA', co2_vol: '2 - 2.5'},
  {name: 'Imperial stout', co2_vol: '2 - 2.5'},
  {name: 'Irish red ale', co2_vol: '2 - 2.5'},
  {name: 'Irish stout', co2_vol: '1.5'},
  {name: 'Kölsch', co2_vol: '2.5'},
  {name: 'Outmeal stout', co2_vol: '2'},
  {name: 'Scottish ale', co2_vol: '1.5 - 2'},
  {name: 'Strong scottish ale', co2_vol: '2 - 2.5'},
];






