import {Component, OnInit} from '@angular/core';
import {Newtonslawofcooling} from '../Models';
import {getTemplateRanges} from 'tslint/lib/rules/noConsecutiveBlankLinesRule';

@Component({
  selector: 'app-newtonslawofcooling',
  templateUrl: './newtonslawofcooling.component.html',
  styleUrls: ['./newtonslawofcooling.component.css']
})
export class NewtonslawofcoolingComponent implements OnInit {

  testings: number;

  newtonslawofcooling: Newtonslawofcooling = {
    temperatureofobject: null,
    time: null,
    ambienttemp: null,
    starttemp: null,
    coolingconstant: null,
    eulers: 2.71828182846,
    calculatedtemp: null,
    desiredtime: null
  };


  constructor() {
  }

  ngOnInit() {
  }


  calculate(starttemp, temperatureofobject, time, ambienttemp, desiredtime) {
   time = time * 60;
    desiredtime = desiredtime * 60;

    this.newtonslawofcooling.coolingconstant = (-(1 / time) * Math.log((temperatureofobject - ambienttemp) / (starttemp - ambienttemp)));
    this.newtonslawofcooling.calculatedtemp = (ambienttemp + ((starttemp - ambienttemp) * (Math.pow(this.newtonslawofcooling.eulers, ((-(this.newtonslawofcooling.coolingconstant) * (desiredtime))))))).toFixed(3 );
  }
}




